___CSS_LOADER_EXPORT___.push([module.id, `/* https://www.schemecolor.com/really-beautiful-pastels.php */
:root {
  --header-background: #1A2238;
  --content-background: #ffffff;
  --footer-background: #1A2238;
  --header-nav-hover: #FF6A3D;
  --transparent-circle: #1A223844;
  --transparent-circle-active: #9DAAF2;
  }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style-type: none;
}

body {
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: 
   'header'
   'content'
   'footer';
}

header {
  background-color: var(--header-background);
  color: white;
  grid-area: header;
}

#content {
  /* Add top & bottom padding to narrow screens */
  /* Removed for wider screens in media query below */
  padding: 5rem 0;
  background-color: var(--content-background);
  grid-area: content;
}

footer {
  background-color: var(--footer-background);
  color: white;
  grid-area: footer;
}

header .title {
  width: 100%;
  text-align: center;
  padding: 1rem;
  padding-bottom: 0;
}

header nav {
  position: relative;
  font-weight: 600;
}

header nav ul {
  display: flex;
}

header nav ul.hide {
  display: none;
}

header nav ul.hidden-menu {
  background-color: var(--header-background);
  width: 100%;
  position: absolute;
  z-index: 1;
}

header nav ul li {
  width: 100%;
  text-align: center;
  cursor: pointer;
  border-radius: 8px;
}

header nav ul li:hover {
  background-color: var(--header-nav-hover);
}

header nav ul li a {
  /* Clickable space should span parent container */
  padding: 1rem;
  width: 100%;
  height: 100%;

  /* Alignment for .dropdown a::after */
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.2rem;
}

header nav ul li.dropdown a::after {
  font-size: 0.6rem;
  /* caret down */
  content: '\\25BC';
}

#content #image-carousel {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  position: relative;
}

#content #image-carousel #images {
  max-width: 90%;
  height: 100%;
}

#content #image-carousel #images img {
  display: none;
  height: 100%;
  width: 100%;
}

#content #image-carousel #images img.active {
  display: block;
}

#content #image-carousel .absolute {
  position: absolute;
}

#content #image-carousel .action#prev {
  bottom: 50%;
  left: 7%;
}

#content #image-carousel .action#next {
  bottom: 50%;
  right: 7%;
}

#content #image-carousel .action#dots {
  bottom: 1rem;
}

#content #image-carousel .circle-background {
  display: flex;
  justify-content: center;
  align-items: center;

  cursor: pointer;
  user-select: none;
  color: white;

  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: var(--transparent-circle);
}

#content #image-carousel .circle-background:hover,
#content #image-carousel #dots .active {
  background-color: var(--transparent-circle-active);
}

#content #image-carousel #dots {
  display: flex;
}

footer {
  width: 100%;
  text-align: center;
  padding: 1rem;
}

footer img {
  height: 2rem;
}

@media only screen and (min-width: 600px) {
  #content {
    padding: 2rem 0;
  }

  #content #image-carousel .circle-background {
    width: 3rem;
    height: 3rem;
  } 
  
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,6DAA6D;AAC7D;EACE,4BAA4B;EAC5B,6BAA6B;EAC7B,4BAA4B;EAC5B,2BAA2B;EAC3B,+BAA+B;EAC/B,oCAAoC;EACpC;;AAEF;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,qBAAqB;EACrB,cAAc;AAChB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,iBAAiB;EACjB,aAAa;EACb,iCAAiC;EACjC;;;WAGS;AACX;;AAEA;EACE,0CAA0C;EAC1C,YAAY;EACZ,iBAAiB;AACnB;;AAEA;EACE,+CAA+C;EAC/C,mDAAmD;EACnD,eAAe;EACf,2CAA2C;EAC3C,kBAAkB;AACpB;;AAEA;EACE,0CAA0C;EAC1C,YAAY;EACZ,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,aAAa;EACb,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,0CAA0C;EAC1C,WAAW;EACX,kBAAkB;EAClB,UAAU;AACZ;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,yCAAyC;AAC3C;;AAEA;EACE,iDAAiD;EACjD,aAAa;EACb,WAAW;EACX,YAAY;;EAEZ,qCAAqC;EACrC,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,iBAAiB;EACjB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,uBAAuB;EACvB,mBAAmB;;EAEnB,kBAAkB;AACpB;;AAEA;EACE,cAAc;EACd,YAAY;AACd;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,WAAW;EACX,QAAQ;AACV;;AAEA;EACE,WAAW;EACX,SAAS;AACX;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;;EAEnB,eAAe;EACf,iBAAiB;EACjB,YAAY;;EAEZ,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,2CAA2C;AAC7C;;AAEA;;EAEE,kDAAkD;AACpD;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,aAAa;AACf;;AAEA;EACE,YAAY;AACd;;AAEA;EACE;IACE,eAAe;EACjB;;EAEA;IACE,WAAW;IACX,YAAY;EACd;;AAEF","sourcesContent":["/* https://www.schemecolor.com/really-beautiful-pastels.php */\r\n:root {\r\n  --header-background: #1A2238;\r\n  --content-background: #ffffff;\r\n  --footer-background: #1A2238;\r\n  --header-nav-hover: #FF6A3D;\r\n  --transparent-circle: #1A223844;\r\n  --transparent-circle-active: #9DAAF2;\r\n  }\r\n\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\na {\r\n  text-decoration: none;\r\n  color: inherit;\r\n}\r